        rabbit.conversation = new ConversationBuilder()
            .say("[i][c=blue]Trois surs, Redina, Greina, Bluella, voyant le nuage s'approcher, se dirigeaient  l'oppose....[/i][/c]")
            .then("Hmmmm... Qu'est-ce que a peut vouloir dire....")
